<template>
  <div>
    <p>Barcode: {{barcode}} <button v-on:click="clearBarcode()">clear</button></p>
    <v-quagga :onProcessed="quagga_OnProcessed" :readerSize="readerSize" :readerTypes="['ean_reader']"></v-quagga>
  </div>
</template>

<script>
import Vue from 'vue'
import VueQuagga from 'vue-quaggajs';

// register component 'v-quagga'
Vue.use(VueQuagga);

export default {
  name: 'VueBarcodeTest',
  data () {
    return {
      readerSize: {
        width: 640,
        height: 480
      },
      detecteds: [],
      barcode: ''
    }
  },
  methods: {
    quagga_OnProcessed (data) {
      if(data && data.codeResult) {
        this.barcode = data.codeResult.code;
      }
    },
    clearBarcode() {
      this.barcode = '';
    }
  }
}
</script>


